@import "variables";
@import "functions";
@import "mixins";
@import "fonts";
@import "keyframes";
@import "global";
.page {
  overflow-x: hidden;
  font-size: 14px;
  line-height: 1.5;
  font-family: "PT Sans", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
  text-transform: uppercase;

  @media ($desktop) {
    background-color: $color-back;
  }
}

.page__body {
  min-width: 320px;
  margin: 0;

  @media ($desktop) {
    width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.page__main {
  display: block;
}

.page__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page__heading {
  display: block;
  margin: 0 24px 27px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;

  @media ($tablet) {
    margin: 0 24px 30px;
    font-size: 30px;
  }
}

.page__description {
  margin: 0 22px 27px;
  line-height: 24px;
  text-align: center;
  color: $color-description;

  @media ($tablet) {
    width: 440px;
    margin: 0 auto 67px;
  }
}

.advantages__description {
  line-height: 26px;
}

.advantages__list {
  border-top: 16px solid $color-accent-back;
  counter-reset: index;
}

.advantages__item {
  padding: 26px 0 0;
  text-align: center;
  background-color: $color-back;
  counter-increment: index;

  &::after {
    content: "";
    display: block;
    margin: 0 20px;
    border-bottom: 1px solid;
    opacity: 0.1;
  }

  &:last-child,
  &--with-img {
    &::after {
      content: none;
    }
  }

  &--with-img {
    padding: 39px 0 0;
    color: $color-accent-text;
    background-color: $color-accent-back;

    .advantages__item-description {
      margin: 0 20px 40px;
      padding: 0;
      color: inherit;
    }
  }
}

.advantages__item-heading {
  margin: 0 20px 10px;
}

.advantages__item-description {
  margin: 0 32px;
  padding: 0 0 28px;
  color: $color-description;

  &::before {
    content: "– №" counter(index) " –";
    display: block;
    margin: 0 0 14px;
  }
}

.advantages__img {
  width: 100%;
}

.button {
  display: block;
  box-sizing: border-box;
  margin: 0 auto;
  font-weight: 700;
  font-family: inherit;
  text-align: center;
  color: $color-accent-text;
  text-transform: uppercase;
  text-decoration: none;
  background-color: $color-accent-back;
  border: none;

  @include interactive;

  &[href]:hover,
  &:not(:disabled):hover,
  &:focus {
    background-color: $color-accent-back-hover;
  }

  &[href]:active,
  &:not(:disabled):active {
    color: rgba($color-accent-text, 0.3);
    background-color: $color-accent-back-active;
  }

  &:disabled {
    background-color: $color-back-disabled;
  }
}

.checker {
  width: 280px;
  margin: 0 auto 9px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;

  label {
    position: relative;
    display: inline-block;
    padding: 17px 0 17px 77px;
    vertical-align: top;

    &::before {
      content: "";
      position: absolute;
      top: 20px;
      left: 17px;
      z-index: 1;
      width: 22px;
      height: 22px;
      background-color: $color-description;
      border-radius: 50%;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      top: 3px;
      left: 0;
      width: 56px;
      height: 56px;
      background-color: $color-back;
      border-radius: 50%;
      transition: background-color 0.3s ease-in-out;
    }


    &:hover,
    &:focus {
      &::after {
        background-color: $color-back-disabled;
      }
    }

    &:active {
      &::before {
        opacity: 0.3;
      }

      &::after {
        background-color: #d5d5d5;
      }
    }
  }

  input:checked + label {
    &::before {
      opacity: 1;
    }
  }

  &--checkbox {
    label {
      padding: 11px 0 34px 78px;

      &::before {
        top: 17px;
        width: 30px;
        height: 17px;
        background-color: transparent;
        background-position: -10px -288px;
      }

      &::after {
        top: -3px;
        border-radius: 5px;
      }
    }

    &:last-child label {
      padding-bottom: 26px;
    }
  }
}

.features {
  padding: 7px 0 37px;
  text-align: left;
  color: #000000;
  background-color: #ffffff;
}

.features__item {
  padding: 28px 28px 0 70px;
}

.features__heading {
  display: block;
  margin: 0 0 14px;
  font-size: 18px;
  line-height: 26px;
}

.features__description {
  margin: 0;
  color: $color-description;
}

.field {
  display: flex;
  line-height: 26px;

  label {
    margin: 18px 15px 0 0;
    color: $color-description;
  }

  textarea {
    height: 128px;
    overflow: auto;
    resize: none;
  }

  &.iconable {
    &::before {
      position: absolute;
      z-index: 1;
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 58px;
      height: 58px;
      background-color: $color-back-darker;
    }

    &--phone::before {
      right: 19px;
      bottom: 20px;
      width: 18px;
      height: 18px;
      background-position: -10px -361px;
    }

    &--mail::before {
      right: 21px;
      bottom: 21px;
      width: 16px;
      height: 16px;
      background-position: -10px -325px;
    }
  }
}

.field__input {
  width: 100%;
  height: 26px;
  padding: 12px 16px;
  font: inherit;
  font-weight: 700;
  font-size: 18px;
  background-color: $color-back;
  border: 4px solid $color-back;
  outline: none;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

  &:hover {
    background-color: $color-back-disabled;
    border-color: $color-back-disabled;
  }

  &:focus {
    background-color: #ffffff;
    border-color: $color-back-disabled;

    &::placeholder {
      color: transparent;
    }
  }

  &::placeholder {
    color: inherit;
    transition: color 0.3s ease-in-out;
  }
}

.footer {
  margin: 0 auto;
  padding: 36px 20px 30px;

  @media ($tablet) {
    display: flex;
    justify-content: space-between;
    width: 648px;
    padding-top: 97px;
  }

  @media ($desktop) {
    width: 960px;
    padding-top: 102px;
  }
}

.footer__developer {
  position: relative;
  display: table;
  margin: 35px auto 0;
  padding: 5px 40px 0 0;

  @media ($tablet) {
    margin: 7px 0 0 auto;
  }
}

.footer__developer-link {
  position: absolute;
  top: 0;
  right: 0;
  width: 27px;
  height: 34px;

  &:hover,
  &:focus {
    color: $color-accent-back;
  }

  &:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

.footer__developer-logo {
  width: inherit;
  height: inherit;
  fill: currentColor;
}

.form {
  margin: -11px 0 0;

  @media ($tablet) {
    display: flex;
    flex-wrap: wrap;
    padding: 0 60px;
  }

  @media ($desktop) {
    padding: 0 120px;
  }
}

.form__group {
  padding: 18px 20px 6px;
  border: none;
  border-top: 1px solid $color-back-darker;

  @media ($tablet) {
    box-sizing: border-box;
    width: 100%;
    margin: 63px 0 0;
  }

  legend {
    margin: 0 0 20px;
    padding: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    background-color: #ffffff;

    @media ($tablet) {
      position: relative;
      top: -31px;
      display: table;
      width: 300px;
      margin: 0 auto;
      padding: 0 50px;
      font-size: 21px;
    }
  }

  &--name {
    @media ($tablet) {
      width: 50%;
      margin: 0;
      padding: 18px 0 0 58px;
      border-top: none;

      legend {
        position: static;
        margin: 0 0 56px;
        padding: 0;
        text-align: left;
      }
    }

    @media ($desktop) {
      padding: 3px 0 0 80px;
    }
  }

  &--impressions {
    padding-bottom: 17px;

    @media ($tablet) {
      position: relative;
      order: -1;
      width: 50%;
      margin: 0;
      padding: 18px 50px 0 0;
      border-top: none;

      &::after {
        content: "";
        position: absolute;
        top: 24px;
        right: 0;
        bottom: 19px;
        width: 1px;
        background-color: #e2e2e2;
      }

      legend {
        position: static;
        margin: 0 0 56px;
        padding: 0;
        text-align: left;
      }

      .form__checker {
        margin: 0 0 18px;
      }
    }

    @media ($desktop) {
      padding: 3px 50px 0 4px;

      &::after {
        top: 8px;
      }

      legend {
        margin: 0 75px 56px;
      }
    }
  }

  &--sights {
    @media ($tablet) {
      margin: 55px 0 -23px;

      legend {
        top: -44px;
      }

      .form__group-inner {
        display: flex;
        flex-wrap: wrap;
        width: 520px;
        margin: 0 auto;
      }

      .form__checker {
        width: 50%;
        margin: 0;
      }

      label {
        margin: 1px 0 16px 31px;
      }
    }

    @media ($desktop) {
      padding: 18px 0 0;

      .form__group-inner {
        justify-content: space-between;
        width: 100%;
      }

      .form__checker {
        width: auto;
      }

      label {
        margin: 1px 5px 20px 0;
      }
    }
  }

  &--message {
    @media ($tablet) {
      padding: 44px 0 0;
    }

    legend {
      margin-bottom: 17px;

      @media ($tablet) {
        top: -57px;
      }
    }

    .form__field {
      @media ($tablet) {
        margin-bottom: 33px;
      }
    }
  }

  fieldset {
    margin: 0;
    padding: 0;
    border: none;
  }
}

.form__group--contacts {
  padding-bottom: 16px;

  @media ($tablet) {
    padding: 11px 0 0;
  }

  .form__group-inner {
    @media ($desktop) {
      display: flex;
      flex-wrap: wrap;
      margin-top: 48px;
    }
  }

  .form__field {
    position: relative;
    flex-wrap: wrap;
    margin: 0 0 12px;

    @media ($tablet) {
      margin: 0 0 23px;
    }

    @media ($desktop) {
      display: flex;

      &:nth-child(odd) {
        margin-right: auto;
      }
    }
  }

  legend {
    @media ($tablet) {
      top: -24px;
    }
  }

  label {
    margin: 0 20px 7px;

    @media ($desktop) {
      margin: 17px 16px 7px 0;
    }
  }

  input {
    padding-right: 78px;

    @media ($desktop) {
      width: 213px;
    }
  }
}

.form__field {
  margin: 0 0 20px;
}

.form__submit-wrapper {
  margin: 25px auto 37px;

  @media ($tablet) {
    display: flex;
    width: 100%;
    margin: 25px 0 0;
  }
}

.form__submit {
  width: 280px;
  padding: 16px 48px;
  font-size: 21px;
  line-height: 26px;

  @media ($tablet) {
    width: 324px;
    margin-right: auto;
    margin-left: 0;
  }

  @media ($desktop) {
    width: 480px;
  }
}

.form__submit-alert {
  max-width: 300px;
  margin: 21px 0 0;

  @media ($mobile) {
    display: none;
  }
}

.header__nav {
  position: relative;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;

  @media ($tablet) {
    line-height: 26px;
  }
}

.header__logo {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);

  @media ($tablet) {
    top: 0;
  }
}

.header__nav-list {
  @media ($tablet) {
    display: flex;
    flex-wrap: wrap;
  }
}

.header__nav-item {
  @media ($mobile) {
    &:first-child .header__nav-link {
      box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2);
    }

    &:last-child .header__nav-link {
      box-shadow: inset 0 -7px 7px -7px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
    }
  }

  @media ($tablet) {
    box-sizing: border-box;
    width: 20%;
    padding: 14px 20px 15px;
    text-align: center;

    &:nth-child(4n + 2) {
      margin-right: 20%;
    }
  }
}

.header__nav-link {
  padding: 8px 60px;

  @media ($mobile) {
    box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }

  @media ($tablet) {
    display: inline;
    padding: 0;
    font-size: 14px;
    color: #000000;
    background-color: transparent;

    &[href]:hover,
    &:focus {
      color: $color-accent-back;
      background-color: transparent;
    }

    &[href]:active {
      color: rgba(#000000, 0.3);
      background-color: transparent;
    }
  }

  &--active {
    @media ($tablet) {
      color: #766357;

      &[href]:hover,
      &:focus {
        color: #604e43;
      }

      &[href]:active {
        color: rgba(118, 99, 87, 0.3);
      }
    }
  }
}

.intro {
  position: relative;
  padding: 197px 0 0;
  border-top: 54px solid transparent;

  @media ($tablet) {
    padding: 365px 0 0;
    border: none;
  }

  @media ($desktop) {
    padding: 413px 0 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 159px;
    background-color: rgba($color-accent-back, 0.5);
    background-image: url("../img/intro-back.jpg");
    background-repeat: no-repeat;
    background-position: 50% 100%;

    @include background-image(intro-back, jpg);

    @media ($tablet) {
      height: 290px;

      @include background-image(intro-back-desktop, jpg);
    }

    @media ($desktop) {
      height: 301px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 133px;
    left: 0;
    width: 100%;
    height: 26px;
    background-image: url("../img/intro-decor.svg");
    background-size: 100% 26px;

    @media ($tablet) {
      top: 233px;
      height: 57px;
      background-image: url("../img/intro-decor-tablet.svg");
      background-size: 100% 57px;
    }

    @media ($desktop) {
      top: 244px;
      background-image: url("../img/intro-decor-desktop.svg");
    }
  }

  &--index {
    padding: 429px 0 0;

    &::before {
      height: 389px;
      background-position: 50% 0;
      background-size: cover;
    }

    &::after {
      top: 363px;
    }

    .intro__img {
      top: 90px;
      width: 281px;
    }
  }
}

.intro__img {
  position: absolute;
  top: 52px;
  left: 50%;
  width: 230px;
  transform: translateX(-50%);

  @media ($tablet) {
    top: 94px;
    width: 460px;
  }

  @media ($desktop) {
    top: 102px;
  }
}

.map__poster {
  position: relative;
  display: block;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.map__img {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}

.search-offer {
  padding: 27px 0 49px;
}

.search-offer__lead {
  display: block;
  margin: 0 20px 19px;

  font-size: 21px;
  line-height: 26px;

  text-align: center;
}

.search-offer__description {
  margin: 0 20px 35px;
}

.search-offer__button {
  width: 280px;
  padding: 16px;
  font-size: 21px;
  line-height: 26px;
}

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.socials__item {
  &--text-link {
    margin: 0 0 36px;

    @media ($mobile) {
      width: 100%;
      text-align: center;
    }

    @media ($tablet) {
      width: 225px;
      margin: 10px 20px 0 0;
    }

    @media ($desktop) {
      width: 382px;
    }
  }
}

.socials__text-link {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-decoration: none;
}

.socials__button {
  width: 46px;
  height: 48px;
  margin: 0 3px 6px;

  &::before {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: inherit;
  }

  &:active::before {
    opacity: 0.3;
  }

  &--twitter::before {
    width: 9px;
    height: 19px;
    background-position: -10px -399px;
  }

  &--facebook::before {
    width: 18px;
    height: 15px;
    background-position: -10px -438px;
  }

  &--youtube::before {
    width: 20px;
    height: 14px;
    background-position: -10px -473px;
  }
}

@import "helpers";
