@charset "UTF-8";

a {
  color: inherit;
  text-decoration-skip-ink: none;
  outline: none;
  cursor: pointer;
  transition-timing-function: ease-in-out;
  transition-duration: 0.3s;
  transition-property: color, opacity, background-color, border-color, box-shadow;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.page {
  overflow-x: hidden;
  font-size: 14px;
  line-height: 1.5;
  font-family: "PT Sans", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
  text-transform: uppercase;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.page__main {
  display: block;
}

.page__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page__heading {
  display: block;
  margin: 0 24px 27px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}

.page__description {
  margin: 0 22px 27px;
  line-height: 24px;
  text-align: center;
  color: #333333;
}

.advantages__description {
  line-height: 26px;
}

.advantages__list {
  border-top: 16px solid #81b3d2;
  counter-reset: index;
}

.advantages__item {
  padding: 26px 0 0;
  text-align: center;
  background-color: #f2f2f2;
  counter-increment: index;
}

.advantages__item::after {
  content: "";
  display: block;
  margin: 0 20px;
  border-bottom: 1px solid;
  opacity: 0.1;
}

.advantages__item:last-child::after,.advantages__item--with-img::after {
  content: none;
}

.advantages__item--with-img {
  padding: 39px 0 0;
  color: #ffffff;
  background-color: #81b3d2;
}

.advantages__item--with-img .advantages__item-description {
  margin: 0 20px 40px;
  padding: 0;
  color: inherit;
}

.advantages__item-heading {
  margin: 0 20px 10px;
}

.advantages__item-description {
  margin: 0 32px;
  padding: 0 0 28px;
  color: #333333;
}

.advantages__item-description::before {
  content: "– №" counter(index) " –";
  display: block;
  margin: 0 0 14px;
}

.advantages__img {
  width: 100%;
}

.button {
  display: block;
  box-sizing: border-box;
  margin: 0 auto;
  font-weight: 700;
  font-family: inherit;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  outline: none;
  cursor: pointer;
  transition-timing-function: ease-in-out;
  transition-duration: 0.3s;
  transition-property: color, opacity, background-color, border-color, box-shadow;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.button[href]:hover,.button:not(:disabled):hover,.button:focus {
  background-color: #669ec0;
}

.button[href]:active,.button:not(:disabled):active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}

.button:disabled {
  background-color: #e5e5e5;
}

.checker {
  width: 280px;
  margin: 0 auto 9px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.checker label {
  position: relative;
  display: inline-block;
  padding: 17px 0 17px 77px;
  vertical-align: top;
}

.checker label::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 17px;
  z-index: 1;
  width: 22px;
  height: 22px;
  background-color: #333333;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.checker label::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out;
}

.checker label:hover::after,.checker label:focus::after {
  background-color: #e5e5e5;
}

.checker label:active::before {
  opacity: 0.3;
}

.checker label:active::after {
  background-color: #d5d5d5;
}

.checker input:checked + label::before {
  opacity: 1;
}

.checker--checkbox label {
  padding: 11px 0 34px 78px;
}

.checker--checkbox label::before {
  top: 17px;
  width: 30px;
  height: 17px;
  background-color: transparent;
  background-position: -10px -288px;
}

.checker--checkbox label::after {
  top: -3px;
  border-radius: 5px;
}

.checker--checkbox:last-child label {
  padding-bottom: 26px;
}

.features {
  padding: 7px 0 37px;
  text-align: left;
  color: #000000;
  background-color: #ffffff;
}

.features__item {
  padding: 28px 28px 0 70px;
}

.features__heading {
  display: block;
  margin: 0 0 14px;
  font-size: 18px;
  line-height: 26px;
}

.features__description {
  margin: 0;
  color: #333333;
}

.field {
  display: flex;
  line-height: 26px;
}

.field label {
  margin: 18px 15px 0 0;
  color: #333333;
}

.field textarea {
  height: 128px;
  overflow: auto;
  resize: none;
}

.field.iconable::before {
  position: absolute;
  z-index: 1;
}

.field.iconable::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
}

.field.iconable--phone::before {
  right: 19px;
  bottom: 20px;
  width: 18px;
  height: 18px;
  background-position: -10px -361px;
}

.field.iconable--mail::before {
  right: 21px;
  bottom: 21px;
  width: 16px;
  height: 16px;
  background-position: -10px -325px;
}

.field__input {
  width: 100%;
  height: 26px;
  padding: 12px 16px;
  font: inherit;
  font-weight: 700;
  font-size: 18px;
  background-color: #f2f2f2;
  border: 4px solid #f2f2f2;
  outline: none;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.field__input:hover {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
}

.field__input:focus {
  background-color: #ffffff;
  border-color: #e5e5e5;
}

.field__input:focus::placeholder {
  color: transparent;
}

.field__input::placeholder {
  color: inherit;
  transition: color 0.3s ease-in-out;
}

.footer {
  margin: 0 auto;
  padding: 36px 20px 30px;
}

.footer__developer {
  position: relative;
  display: table;
  margin: 35px auto 0;
  padding: 5px 40px 0 0;
}

.footer__developer-link {
  position: absolute;
  top: 0;
  right: 0;
  width: 27px;
  height: 34px;
}

.footer__developer-link:hover,.footer__developer-link:focus {
  color: #81b3d2;
}

.footer__developer-link:active {
  color: rgba(0, 0, 0, 0.3);
}

.footer__developer-logo {
  width: inherit;
  height: inherit;
  fill: currentColor;
}

.form {
  margin: -11px 0 0;
}

.form__group {
  padding: 18px 20px 6px;
  border: none;
  border-top: 1px solid #e6e6e6;
}

.form__group legend {
  margin: 0 0 20px;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  background-color: #ffffff;
}

.form__group--impressions {
  padding-bottom: 17px;
}

.form__group--message legend {
  margin-bottom: 17px;
}

.form__group fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.form__group--contacts {
  padding-bottom: 16px;
}

.form__group--contacts .form__field {
  position: relative;
  flex-wrap: wrap;
  margin: 0 0 12px;
}

.form__group--contacts label {
  margin: 0 20px 7px;
}

.form__group--contacts input {
  padding-right: 78px;
}

.form__field {
  margin: 0 0 20px;
}

.form__submit-wrapper {
  margin: 25px auto 37px;
}

.form__submit {
  width: 280px;
  padding: 16px 48px;
  font-size: 21px;
  line-height: 26px;
}

.form__submit-alert {
  max-width: 300px;
  margin: 21px 0 0;
}

.header__nav {
  position: relative;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;
}

.header__logo {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
}

.header__nav-link {
  padding: 8px 60px;
}

.intro {
  position: relative;
  padding: 197px 0 0;
  border-top: 54px solid transparent;
}

.intro::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 159px;
  background-color: rgba(129, 179, 210, 0.5);
  background-image: url("../img/intro-back.jpg");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-image: url("../img/intro-back.jpg");
}

.intro::after {
  content: "";
  position: absolute;
  top: 133px;
  left: 0;
  width: 100%;
  height: 26px;
  background-image: url("../img/intro-decor.svg");
  background-size: 100% 26px;
}

.intro--index {
  padding: 429px 0 0;
}

.intro--index::before {
  height: 389px;
  background-position: 50% 0;
  background-size: cover;
}

.intro--index::after {
  top: 363px;
}

.intro--index .intro__img {
  top: 90px;
  width: 281px;
}

.intro__img {
  position: absolute;
  top: 52px;
  left: 50%;
  width: 230px;
  transform: translateX(-50%);
}

.map__poster {
  position: relative;
  display: block;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.map__img {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}

.search-offer {
  padding: 27px 0 49px;
}

.search-offer__lead {
  display: block;
  margin: 0 20px 19px;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
}

.search-offer__description {
  margin: 0 20px 35px;
}

.search-offer__button {
  width: 280px;
  padding: 16px;
  font-size: 21px;
  line-height: 26px;
}

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.socials__item--text-link {
  margin: 0 0 36px;
}

.socials__text-link {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-decoration: none;
}

.socials__button {
  width: 46px;
  height: 48px;
  margin: 0 3px 6px;
}

.socials__button::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: inherit;
}

.socials__button:active::before {
  opacity: 0.3;
}

.socials__button--twitter::before {
  width: 9px;
  height: 19px;
  background-position: -10px -399px;
}

.socials__button--facebook::before {
  width: 18px;
  height: 15px;
  background-position: -10px -438px;
}

.socials__button--youtube::before {
  width: 20px;
  height: 14px;
  background-position: -10px -473px;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: fixed;
  top: 200vh;
}

.iconable {
  position: relative;
}

.iconable::before {
  content: "";
  position: absolute;
  background-image: url("../img/icons.svg");
  background-repeat: no-repeat;
  background-size: 96.17px 498px;
}

@font-face {
  font-style:normal;font-weight:400;font-family:"PT Sans";font-display:swap;src:local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}

@font-face {
  font-style:normal;font-weight:700;font-family:"PT Sans";font-display:swap;src:local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}

@media (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
  .intro::before {
    background-image: url("../img/intro-back.webp");
  }
}

@media (min-width: 768px) {
  .page__heading {
    margin: 0 24px 30px;
    font-size: 30px;
  }

  .page__description {
    width: 440px;
    margin: 0 auto 67px;
  }

  .footer {
    display: flex;
    justify-content: space-between;
    width: 648px;
    padding-top: 97px;
  }

  .footer__developer {
    margin: 7px 0 0 auto;
  }

  .form {
    display: flex;
    flex-wrap: wrap;
    padding: 0 60px;
  }

  .form__group {
    box-sizing: border-box;
    width: 100%;
    margin: 63px 0 0;
  }

  .form__group legend {
    position: relative;
    top: -31px;
    display: table;
    width: 300px;
    margin: 0 auto;
    padding: 0 50px;
    font-size: 21px;
  }

  .form__group--name {
    width: 50%;
    margin: 0;
    padding: 18px 0 0 58px;
    border-top: none;
  }

  .form__group--name legend {
    position: static;
    margin: 0 0 56px;
    padding: 0;
    text-align: left;
  }

  .form__group--impressions {
    position: relative;
    order: -1;
    width: 50%;
    margin: 0;
    padding: 18px 50px 0 0;
    border-top: none;
  }

  .form__group--impressions::after {
    content: "";
    position: absolute;
    top: 24px;
    right: 0;
    bottom: 19px;
    width: 1px;
    background-color: #e2e2e2;
  }

  .form__group--impressions legend {
    position: static;
    margin: 0 0 56px;
    padding: 0;
    text-align: left;
  }

  .form__group--impressions .form__checker {
    margin: 0 0 18px;
  }

  .form__group--sights {
    margin: 55px 0 -23px;
  }

  .form__group--sights legend {
    top: -44px;
  }

  .form__group--sights .form__group-inner {
    display: flex;
    flex-wrap: wrap;
    width: 520px;
    margin: 0 auto;
  }

  .form__group--sights .form__checker {
    width: 50%;
    margin: 0;
  }

  .form__group--sights label {
    margin: 1px 0 16px 31px;
  }

  .form__group--message {
    padding: 44px 0 0;
  }

  .form__group--message legend {
    top: -57px;
  }

  .form__group--message .form__field {
    margin-bottom: 33px;
  }

  .form__group--contacts {
    padding: 11px 0 0;
  }

  .form__group--contacts .form__field {
    margin: 0 0 23px;
  }

  .form__group--contacts legend {
    top: -24px;
  }

  .form__submit-wrapper {
    display: flex;
    width: 100%;
    margin: 25px 0 0;
  }

  .form__submit {
    width: 324px;
    margin-right: auto;
    margin-left: 0;
  }

  .header__nav {
    line-height: 26px;
  }

  .header__logo {
    top: 0;
  }

  .header__nav-list {
    display: flex;
    flex-wrap: wrap;
  }

  .header__nav-item {
    box-sizing: border-box;
    width: 20%;
    padding: 14px 20px 15px;
    text-align: center;
  }

  .header__nav-item:nth-child(4n + 2) {
    margin-right: 20%;
  }

  .header__nav-link {
    display: inline;
    padding: 0;
    font-size: 14px;
    color: #000000;
    background-color: transparent;
  }

  .header__nav-link[href]:hover,.header__nav-link:focus {
    color: #81b3d2;
    background-color: transparent;
  }

  .header__nav-link[href]:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }

  .header__nav-link--active {
    color: #766357;
  }

  .header__nav-link--active[href]:hover,.header__nav-link--active:focus {
    color: #604e43;
  }

  .header__nav-link--active[href]:active {
    color: rgba(118, 99, 87, 0.3);
  }

  .intro {
    padding: 365px 0 0;
    border: none;
  }

  .intro::before {
    height: 290px;
    background-image: url("../img/intro-back-desktop.jpg");
  }

  .intro::after {
    top: 233px;
    height: 57px;
    background-image: url("../img/intro-decor-tablet.svg");
    background-size: 100% 57px;
  }

  .intro__img {
    top: 94px;
    width: 460px;
  }

  .socials__item--text-link {
    width: 225px;
    margin: 10px 20px 0 0;
  }
}

@media (min-width: 768px) and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
  .intro::before {
    background-image: url("../img/intro-back-desktop.webp");
  }
}

@media (min-width: 1200px) {
  .page {
    background-color: #f2f2f2;
  }

  .page__body {
    width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }

  .footer {
    width: 960px;
    padding-top: 102px;
  }

  .form {
    padding: 0 120px;
  }

  .form__group--name {
    padding: 3px 0 0 80px;
  }

  .form__group--impressions {
    padding: 3px 50px 0 4px;
  }

  .form__group--impressions::after {
    top: 8px;
  }

  .form__group--impressions legend {
    margin: 0 75px 56px;
  }

  .form__group--sights {
    padding: 18px 0 0;
  }

  .form__group--sights .form__group-inner {
    justify-content: space-between;
    width: 100%;
  }

  .form__group--sights .form__checker {
    width: auto;
  }

  .form__group--sights label {
    margin: 1px 5px 20px 0;
  }

  .form__group--contacts .form__group-inner {
    display: flex;
    flex-wrap: wrap;
    margin-top: 48px;
  }

  .form__group--contacts .form__field {
    display: flex;
  }

  .form__group--contacts .form__field:nth-child(odd) {
    margin-right: auto;
  }

  .form__group--contacts label {
    margin: 17px 16px 7px 0;
  }

  .form__group--contacts input {
    width: 213px;
  }

  .form__submit {
    width: 480px;
  }

  .intro {
    padding: 413px 0 0;
  }

  .intro::before {
    height: 301px;
  }

  .intro::after {
    top: 244px;
    background-image: url("../img/intro-decor-desktop.svg");
  }

  .intro__img {
    top: 102px;
  }

  .socials__item--text-link {
    width: 382px;
  }

  .no-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .form__submit-alert {
    display: none;
  }

  .header__nav-item:first-child .header__nav-link {
    box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2);
  }

  .header__nav-item:last-child .header__nav-link {
    box-shadow: inset 0 -7px 7px -7px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }

  .header__nav-link {
    box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }

  .socials__item--text-link {
    width: 100%;
    text-align: center;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  100% {
    opacity: 0;
  }
}
